import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Link from '@mui/material/Link';
import React from 'react';
import FilledButton from './FilledButton.js';

class LandingDialog extends React.Component {
  constructor( props ) {
    super( props );

    this.state = {
      open: true
    };
  }

  handleDisagree() {

    // I guess just close the dialog? Ideally navigate to a page but that is difficult for some reason.
    this.setState( { open: false } );
  }

  handleAgree() {

    // start everything by kicking off the webcam
    this.props.mediaPipeManager.init( document.getElementById( 'input-video' ) );

    this.setState( { open: false } );
  }

  render() {
    return (
      <Dialog
        open={this.state.open}
        fullWidth={true}
        maxWidth={'md'}
      >
        <DialogTitle>
          {"Titan"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            This app uses your webcam and plays audio.
          </DialogContentText>
          <br/>
          <DialogContentText>
            It uses machine learning to detect your hand gestures and plays a chord for each of the gestures it finds. Follow the ghost hand to play "Titan" by Disasterpeace!
          </DialogContentText>
          <br/>
          <DialogContentText>
            Hand detection with <Link color='primary.light' target="_blank" rel="noreferrer" href="https://google.github.io/mediapipe/solutions/hands.html">MediaPipe.</Link>
            <br/>
            Neural network trained with <Link color='primary.light' target="_blank" rel="noreferrer" href="https://ml5js.org/">ml5.js.</Link>
          </DialogContentText>
          <br/>
          <DialogContentText>
            Artwork and sound from the game Hyper Light Drifter by <Link color='primary.light' href="https://disasterpeace.com/" target="_blank" rel="noreferrer">Heart Machine.</Link> This is not a Heart Machine product nor sponsored by Heart Machine.
          </DialogContentText>
          <DialogContentText>
            Music from the song "Titan" by the artist <Link color='primary.light' href="https://disasterpeace.com/" target="_blank" rel="noreferrer">Disasterpeace.</Link>
          </DialogContentText>
          <DialogContentText>
            Background sounds by <Link color='primary.light' target="_blank" rel="noreferrer" href="https://pixabay.com/users/juliush-3921568/?tab=audio">JuliusH</Link> from Pixabay.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <FilledButton enabled={true} content="No thanks. Don't use my webcam." onClick={() => this.handleDisagree()}></FilledButton>
          <FilledButton enabled={true} content="OK!" onClick={() => this.handleAgree()} autoFocus>
          </FilledButton>
        </DialogActions>
      </Dialog>
    );
  }
}

export default LandingDialog;