import React from 'react';
import HandCanvas from './HandCanvas.js';
import LinearFunction from './LinearFunction.js';
import Vector3 from './Vector3.js';

// How long it takes to move across the width canvas, which has a normalized width of 1.
const LANDMARK_SPEED = 3; // screens widths per second
const MAX_SPEED_PER_TIME_STEP = 1 / 60 * LANDMARK_SPEED;

const DISTANCE_SPEED_FUNCTION = new LinearFunction( 0, 1, 0, MAX_SPEED_PER_TIME_STEP, true );

class AnimatedHandCanvas extends React.Component {
  constructor( props ) {
    super( props );

    this.state = {
      intermediateLandmarks: [],
    }

    window.setInterval( () => {
      const intermediateLandmarks = this.getIntermediateLandmarks();
      this.setState( {
        intermediateLandmarks: intermediateLandmarks
      } );
    }, 1 / 60 );
  }

  getIntermediateLandmarks() {

    const intermediateLandmarks = [];
    this.props.destinationLandmarks.forEach( ( destinationLandmark, index ) => {
      const intermediateLandmark = new Vector3( 0, 0, 0 );

      const currentLandmark = this.state.intermediateLandmarks[ index ];
      if ( !currentLandmark ) {

        // no current landmark to animate from, just set to destination
        intermediateLandmark.setXYZ( destinationLandmark.x, destinationLandmark.y, destinationLandmark.z );
      }
      else {
        // get a vector from the current landmark to the destination landmark
        const distanceX = destinationLandmark.x - currentLandmark.x;
        const distanceY = destinationLandmark.y - currentLandmark.y;
        const distanceZ = destinationLandmark.z - currentLandmark.z;

        const distanceVector = new Vector3( distanceX, distanceY, distanceZ );
        const distanceMagnitude = distanceVector.getMagnitude();
        if ( distanceMagnitude > 0.01 ) {

          const translationMagnitude = DISTANCE_SPEED_FUNCTION.evaluate( distanceMagnitude );
          const translationVector = distanceVector.withMagnitude( translationMagnitude );

          intermediateLandmark.set( translationVector.plus( currentLandmark ) );
        }
        else {
          intermediateLandmark.set( currentLandmark );
        }
      }

      // duck typing with our Vector3 (yuck)
      intermediateLandmarks.push( { x: intermediateLandmark.x, y: intermediateLandmark.y, z: intermediateLandmark.z } );
    } );

    return intermediateLandmarks;
  }

  render() {
    return (
      <HandCanvas
        landmarks={this.state.intermediateLandmarks}
        landmarkColor={this.props.landmarkColor}
        connectorColor={this.props.connectorColor}
      ></HandCanvas>
    )
  }
}

export default AnimatedHandCanvas;