import CircularProgress from '@mui/material/CircularProgress';
import React from 'react';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';

class CircularProgressWithLabel extends React.Component {
  constructor( props ) {
    super( props );
  }

  render() {
    return (
      <Stack sx={{ position: 'relative', display: 'inline-flex' }} direction='row'>
        <CircularProgress size={50} variant="determinate" {...this.props} />
        <Box
          sx={{
            top: 0,
            left: 0,
            bottom: 0,
            right: 0,
            position: 'absolute',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          <Typography
            variant="caption"
            component="div"
            color="text.secondary"
          >{`${Math.round( this.props.value )}%`}</Typography>
        </Box>
      </Stack>
    );
  }
}

export default CircularProgressWithLabel;
