import Stack from '@mui/material/Stack';
import React from 'react';
import AnimatedHandCanvas from './AnimatedHandCanvas.js';

import CircularProgressWithLabel from './CircularProgressWithLabel.js';
import FilledButton from './FilledButton.js';
import HandCanvas from './HandCanvas.js';
import titanTheme from './titanTheme.js';

const MAX_SAMPLES = 150;

class GestureTrainingDisplay extends React.Component {
  constructor( props ) {
    super( props );

    const classificationManager = this.props.classificationManager;
    const mediaPipeManager = this.props.mediaPipeManager;
    const selectedGesture = classificationManager.selectedGestureProperty.value;

    const initialGestureData = classificationManager.getGestureData( selectedGesture );
    const initialNumberOfSamples = initialGestureData ? initialGestureData.length : 0;

    // State is used to re-render components, much is copied from model Properties
    this.state = {

      // state managed only by this component
      recording: false,

      // state controlled by other model components
      selectedGesture: selectedGesture,
      numberOfSamples: initialNumberOfSamples,
      drawingHandLandmarks: [],
      classification: { label: null, confidence: 0 },
      nnTrained: this.props.classificationManager.nnTrainedProperty.value,
      nnTraining: this.props.classificationManager.nnTrainingProperty.value
    }

    this.props.classificationManager.nnTrainedProperty.lazyLink( nnTrained => {
      this.setState( { nnTrained: nnTrained } );
    } );

    this.props.classificationManager.nnTrainingProperty.lazyLink( nnTraining => {
      this.setState( { nnTraining: nnTraining } );
    } );

    // Display new selected gesture and number of saved samples when selected gesture changes.
    classificationManager.selectedGestureProperty.lazyLink( newGesture => {
      this.setState( {
        selectedGesture: newGesture,
        numberOfSamples: classificationManager.getGestureData( newGesture ).length
      } );
    } );

    // Display the updated number of samples when the number of samples changes
    classificationManager.trainingModelChangedEmitter.addListener( () => {
      const newGesture = classificationManager.selectedGestureProperty.value;
      this.setState( {
        numberOfSamples: classificationManager.getGestureData( newGesture ).length
      } );
    } );

    mediaPipeManager.drawingLandmarksProperty.lazyLink( newLandmarks => {
      this.setState( {
        drawingHandLandmarks: newLandmarks
      } );
    } );

    classificationManager.classificationProperty.lazyLink( classification => {
      this.setState( { classification: classification } );
    } );
  }

  /**
   * Controls whether we are recording new data. Listener for the record button, will continue to record
   * until the mouse is up.
   */
  handlePointerDown() {

    const windowListener = () => {
      this.setState( { recording: false } );
      this.props.classificationManager.recordingProperty.value = false;
      window.removeEventListener( 'pointerup', windowListener );
    }

    window.addEventListener( 'pointerup', windowListener );
    this.setState( { recording: true } );
    this.props.classificationManager.recordingProperty.value = true;
  }

  /**
   * Clears all data for the provided gesture. Listener for the clear samples button.
   */
  handleClearClick() {
    this.props.classificationManager.removeAllGestureData( this.props.classificationManager.selectedGestureProperty.value );
  }

  render() {
    const classificationManager = this.props.classificationManager;

    const selectedGesture = this.state.selectedGesture;
    const numberOfSamples = this.state.numberOfSamples;
    const exampleLandmarks = classificationManager.getExampleLandmarksFromGesture( selectedGesture ) || [];

    const ratioFull = Math.round( ( numberOfSamples / MAX_SAMPLES ) * 100 );

    const classification = this.state.classification;
    const confidencePercent = ( classification.confidence * 100 ).toFixed( 0 );

    let outputString = '';
    if ( this.state.nnTraining ) {
      outputString = 'Training...';

    }
    else if ( this.state.nnTrained ) {
      outputString = `Classified Gesture: ${classification.label} (${confidencePercent}%)`;

    }
    else {
      outputString = 'Train the neural network.'
    }

    return (
      <div>
        <Stack direction="row" spacing={2}>
          {/*This div is necessary for layout under the stack, or the canvas is a few pixels too tall for some reason*/}
          <div>
            <AnimatedHandCanvas
              destinationLandmarks={exampleLandmarks}
              landmarkColor={titanTheme.palette.primary.dark}
              connectorColor={titanTheme.palette.primary.light}
            ></AnimatedHandCanvas>
          </div>
          <div>
            <HandCanvas
              landmarks={this.state.drawingHandLandmarks} // TODO: To come from MediaPipeManager
              landmarkColor={titanTheme.palette.primary.dark}
              connectorColor={titanTheme.palette.primary.light}
            ></HandCanvas>
          </div>
          <Stack direction="column" spacing={2}>
            <Stack direction="row" spacing={2}>
              <FilledButton enabled={numberOfSamples < MAX_SAMPLES} content="Hold to record" onPointerDown={() => this.handlePointerDown()}></FilledButton>
              <FilledButton enabled={numberOfSamples > 0} content="Clear samples" onClick={() => this.handleClearClick()}></FilledButton>
            </Stack>
            <Stack direction="row" spacing={2} alignItems="center">
              <p>{`Number of samples: ${numberOfSamples}`}</p>
              <CircularProgressWithLabel value={ratioFull}></CircularProgressWithLabel>
            </Stack>
            <p>{outputString}</p>
          </Stack>
        </Stack>
      </div>
    );
  }
}

export default GestureTrainingDisplay;