import Box from '@mui/material/Box';
import Checkbox from '@mui/material/Checkbox';
import Container from '@mui/material/Container';
import Divider from '@mui/material/Divider';
import Fade from '@mui/material/Fade';
import FormControlLabel from '@mui/material/FormControlLabel';
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import React from 'react';
import AnimatedHandCanvas from './AnimatedHandCanvas.js';
import BackgroundSoundRadioGroup from './BackgroundSoundRadioGroup.js';
import HandCanvas from './HandCanvas.js';
import fireBackground from './images/fire-background.gif';
import rainBackground from './images/rain-background.gif';
import waterfallBackground from './images/waterfall-background.gif';
import noneBackground from './images/none-background.gif';
import ThickLinearProgressBar from './ThickLinearProgressBar.js';
import TitanConstants from './TitanConstants.js';
import TitanSoundClipPlayer from './TitanSoundClipPlayer.js';
import titanTheme from './titanTheme.js';

const STABLE_CLASSIFICATION_INTERVAL = 600;

class TitanScreen extends React.Component {
  constructor( props ) {
    super( props );

    this.state = {
      //-----------------------------------------------------------
      // state properties that are totally managed by this component
      //-----------------------------------------------------------
      background: TitanConstants.BACKGROUND_NONE,
      currentGhostGesture: TitanConstants.TITAN_SONG_GESTURE_ORDER[ 0 ],
      ghostHandVisible: true,

      // Label to display when a gesture has been held for long enough to be considered a detection
      // Not a string, one of the enueration values for gestures
      stableGestureLabel: null,

      // Value from 0 to 100 (range of LinearProgress) indicating how far we are into playing a gesture sound.
      gestureClipProgress: 0,

      // The displayed gesture label, separate from the classified label because this is displayed for longer than
      // the actual classified gesture may be shown when stable gesture becomes null. On of the constants like
      // 'GESTURE_1', 'GESTURE_2' and so on.
      displayedGestureLabel: null,

      //-------------------------------------------------------------------
      // state properties that are mostly managed by other model components
      //-------------------------------------------------------------------
      // classification: { label: null, results: null },
      drawingLandmarks: []
    }

    this.props.classificationManager.classificationProperty.lazyLink( classification => {

      // whenever classification changes, it is no longer stable, reset this timer
      this.stableTime = 0;
    } );

    this.props.mediaPipeManager.drawingLandmarksProperty.lazyLink( landmarks => {
      this.setState( { drawingLandmarks: landmarks } );
    } );

    // Manages sounds for a gesture
    this.soundPlayer = new TitanSoundClipPlayer();
    TitanSoundClipPlayer.init();

    // Amount of time that has
    this.stableTime = 0;

    // to track how much time passes each time step

    // TODO: Refactor to an animation hook
    this.startTime = Date.now();

    this.boundStep = this.step.bind( this );
    this.boundHandleBackgroundChange = this.handleBackgroundChange.bind( this );

    // Use the animation frame to determine stability of the classification - it has to be stable for a certain
    // amount of time for a response from the application.
    const timer = window.requestAnimationFrame( this.boundStep );
  }

  step() {

    // in ms
    const now = Date.now();
    const dt = now - this.startTime;
    this.startTime = now;

    // don't do anything for extreme time steps (switched tabs or something)
    if ( dt < 1000 ) {
      this.stableTime += dt;

      // Collect changes in one new state object so each frame we only have one call to setState.
      const newState = {};

      const newClassification = this.props.classificationManager.classificationProperty.value;
      if ( this.stableTime > STABLE_CLASSIFICATION_INTERVAL && this.state.stableGestureLabel !== newClassification.label ) {

        const gestureValueToDisplay = newClassification.label ? newClassification.label : this.state.stableGestureLabel;

        newState[ 'stableGestureLabel' ] = newClassification.label;

        // if the classification has null label, display the old one to let it fade out, otherwise show the
        // new gesture label.
        newState[ 'displayedGestureLabel' ] = gestureValueToDisplay;
      }

      const playingSoundProperties = this.soundPlayer.getGestureAudioTimeProperties();
      let progress = 0;
      if ( playingSoundProperties ) {
        progress = ( playingSoundProperties.currentTime / playingSoundProperties.duration ) * 100;

        const remainingSoundTime = playingSoundProperties.duration - playingSoundProperties.currentTime;
        if ( playingSoundProperties.playingGesture === this.state.currentGhostGesture && remainingSoundTime < 4 ) {

          // We are playing the sound of the ghost gesture and it is wrapping up, it is time to show the next hand
          // gesture in the play order
          const indexOfCurrentHandGesture = TitanConstants.TITAN_SONG_GESTURE_ORDER.indexOf( this.state.currentGhostGesture );

          // wrap around to the start of the song
          const nextIndex = indexOfCurrentHandGesture + 1;
          const indexOfNextHandGesture = nextIndex === TitanConstants.TITAN_SONG_GESTURE_ORDER.length ? 0 : nextIndex;

          newState[ 'currentGhostGesture' ] = TitanConstants.TITAN_SONG_GESTURE_ORDER[ indexOfNextHandGesture ];
        }
      }

      newState[ 'gestureClipProgress' ] = progress;

      // set state once after all updates.
      this.setState( newState );
    }

    requestAnimationFrame( this.boundStep );
  }

  componentDidUpdate( prevProps, prevState, snapshot ) {
    if ( prevState.stableGestureLabel !== this.state.stableGestureLabel ) {

      // A new stable gesture! Play a new sound.
      this.soundPlayer.playGestureSound( this.state.stableGestureLabel );
    }
  }

  componentWillUnmount() {

    // Component is about to unmount, set background back to default for next time this screen is used and
    // to stop playing background sounds
    this.handleBackgroundChange( null, TitanConstants.BACKGROUND_NONE );
  }

  handleBackgroundChange( event, value ) {
    this.soundPlayer.loopBackgroundSound( value );

    this.setState( {
      background: value
    } );
  }

  render() {
    const labelTextIn = !!this.state.stableGestureLabel;

    // The current gesture, previous one (to fade out) or nothing in fall back case.
    const displayedGestureLabel = TitanConstants.GESTURE_NAME_MAP.get( this.state.displayedGestureLabel ) || 'FALLBACK';

    const ghostDestinationLandmarks = this.props.classificationManager.getExampleLandmarksFromGesture( this.state.currentGhostGesture );

    const backgroundSrc = this.state.background === TitanConstants.BACKGROUND_WATERFALL ? waterfallBackground :
                          this.state.background === TitanConstants.BACKGROUND_RAIN ? rainBackground :
                          this.state.background === TitanConstants.BACKGROUND_FIRE ? fireBackground :
                          noneBackground; // for 'none' and 'waterfall'

    const ghostElement = this.state.ghostHandVisible ?
                         ( <AnimatedHandCanvas
                           destinationLandmarks={ghostDestinationLandmarks}
                           landmarkColor={titanTheme.palette.primary.dark}
                           connectorColor={titanTheme.palette.primary.light}
                         ></AnimatedHandCanvas> ) : ( <div></div> );

    return (
      <div>
        <Grid container spacing={0} padding={2}>
          <Grid item xs={2}>
            <Stack direction='column' spacing={2} divider={<Divider orientation="horizontal" flexItem/>}>
              <BackgroundSoundRadioGroup
                handleChange={this.boundHandleBackgroundChange}
              ></BackgroundSoundRadioGroup>
              <FormControlLabel
                control={
                  <Checkbox
                    defaultChecked
                    onChange={( event ) => { this.setState( { ghostHandVisible: event.target.checked } )}}
                  />
                }
                label="Ghost Hand"/>
            </Stack>
          </Grid>
          <Grid item xs={8}>
            <Container>
              <Stack direction='column' alignItems='center' spacing={1}>
                <img src={backgroundSrc} alt="" width='90%'/>
                <Stack direction='row' spacing={10}>
                  {ghostElement}
                  <Box width={300} justifyContent='center' display='flex'>
                    <Stack direction='column' spacing={5} justifyContent='center' alignItems='center'>
                      <Fade timeout={3000} in={labelTextIn}>
                        <Typography
                          component="div"
                          color="text.secondary"
                        >{displayedGestureLabel}</Typography>
                      </Fade>
                      <ThickLinearProgressBar variant="determinate" color="secondary" sx={{ width: '300px' }} value={this.state.gestureClipProgress}/>
                    </Stack>
                  </Box>
                  <HandCanvas
                    landmarks={this.state.drawingLandmarks}
                    landmarkColor={titanTheme.palette.secondary.dark}
                    connectorColor={titanTheme.palette.secondary.light}
                  ></HandCanvas>
                </Stack>
              </Stack>
            </Container>
          </Grid>
          <Grid item xs={2} id={'important-for-layout'}></Grid>
        </Grid>
      </div>
    )
  }
}

export default TitanScreen;