import Container from '@mui/material/Container';
import Stack from '@mui/material/Stack';
import React from 'react';
import GestureSelect from './GestureSelect.js';
import GestureTrainingDisplay from './GestureTrainingDisplay.js';
import TooltipButton from './TooltipButton.js';

class ModelControls extends React.Component {
  constructor( props ) {
    super( props );

    // state used to render
    this.state = {
      nnCreated: this.props.classificationManager.nnCreatedProperty.value,
      nnTrained: this.props.classificationManager.nnTrainedProperty.value,
      nnTraining: this.props.classificationManager.nnTrainingProperty.value
    }

    this.props.classificationManager.nnCreatedProperty.lazyLink( created => {
      this.setState( { nnCreated: created } );
    } );

    this.props.classificationManager.nnTrainedProperty.lazyLink( trained => {
      this.setState( { nnTrained: trained } );
    } );

    this.props.classificationManager.nnTrainingProperty.lazyLink( training => {
      this.setState( { nnTraining: training } );
    } );
  }

  render() {
    const classificationManager = this.props.classificationManager;

    return (
      <div>
        <Stack spacing={2} direction="row">
          <TooltipButton
            content="Download JSON model"
            onClick={() => classificationManager.saveModelJSON()}
            tooltipContent="Save recorded hand positions to a large JSON file that can be loaded later. Move from downloads to titan/public/json-model."
            enabled={true}
          >Download JSON model</TooltipButton>
          <TooltipButton
            content="Train ML model"
            onClick={() => classificationManager.trainMLModel()}
            tooltipContent="Start training a new neural network."
            enabled={!this.state.nnTraining}
          >Train ML model</TooltipButton>
          <TooltipButton
            content="Save ML Model"
            onClick={() => classificationManager.saveMLModel()}
            tooltipContent="Save the neural network model files. Move from downloads to titan/public/trained-model/."
            enabled={this.state.nnTrained}
          >Save ML Model</TooltipButton>
          <TooltipButton
            content="Load ML Model"
            onClick={() => classificationManager.loadMLModel()}
            tooltipContent="Load a neural network model if you have one saved. Must have pre-saved files in titan/public/trained-model/."
            enabled={this.state.nnCreated && !this.state.nnTraining && !this.state.nnTrained }
          >Load ML Model</TooltipButton>
        </Stack>
      </div>
    )
  }
}

class TrainingScreen extends React.Component {
  constructor( props ) {
    super( props );
  }

  render() {
    return (
      <div>
        <Container sx={{ margin: 2 }}>
          <GestureSelect
            selectedGestureProperty={this.props.classificationManager.selectedGestureProperty}
          />
        </Container>
        <Stack
          spacing={5}
          padding={5}
          direction='column'
          alignItems='center'
        >
          <GestureTrainingDisplay
            classificationManager={this.props.classificationManager}
            mediaPipeManager={this.props.mediaPipeManager}
          />
          <ModelControls
            classificationManager={this.props.classificationManager}
          />
        </Stack>
      </div>
    );
  }
}

export default TrainingScreen;