class Vector3 {

  constructor( x, y, z ) {
    this.x = x;
    this.y = y;
    this.z = z;
  }

  getMagnitude() {
    const magnitudeSquared = this.x * this.x + this.y * this.y + this.z * this.z;
    return Math.sqrt( magnitudeSquared );
  }

  multiplyScalar( scalar ) {
    return this.setXYZ( this.x * scalar, this.y * scalar, this.z * scalar );
  }

  setXYZ( x, y, z ) {
    this.x = x;
    this.y = y;
    this.z = z;
  }

  copy() {
    return new Vector3( this.x, this.y, this.z );
  }

  setMagnitude( magnitude ) {
    const scale = magnitude / this.getMagnitude();
    this.multiplyScalar( scale );
    return this;
  }

  withMagnitude( magnitude ) {
    return this.copy().setMagnitude( magnitude );
  }

  plus( vector ) {
    return new Vector3( this.x + vector.x, this.y + vector.y, this.z + vector.z );
  }

  minus( vector ) {
    return new Vector3( this.x - vector.x, this.y - vector.y, this.z - vector.z );
  }

  set( vector ) {
    this.setXYZ( vector.x, vector.y, vector.z );
  }

}

export default Vector3;
