import Tooltip from '@mui/material/Tooltip';
import React from 'react';
import FilledButton from './FilledButton.js';

class TooltipButton extends React.Component {

  /**
   * Expected props:
   * @props-extends FilledButton
   * @prop {string} title - callback for the pointerDown event
   * @param props
   */
  constructor( props ) {
    super( props );
  }

  render() {
    return (
      <Tooltip title={this.props.tooltipContent}>
        <div>
          <FilledButton {...this.props}/>
        </div>
      </Tooltip>
    )
  }
}

export default TooltipButton;