import Button from '@mui/material/Button';
import React from 'react';

class FilledButton extends React.Component {

  /**
   * Expected props:
   * @prop {string} content - innerContent of the button
   * @prop {function} [onClick] - callback for click event
   * @prop {function} [onPointerDown] - callback for the pointerDown event
   * @param props
   */
  constructor( props ) {
    super( props );
  }

  setProp( prop, value, container ) {
    container[ prop ] = value;
  }

  render() {
    const innerContent = this.props.content;

    // TODO: Instead of this, maybe just pass this.props to the <Button>?
    const inputProps = {
      content: this.props.content,
      disabled: !this.props.enabled
    };
    this.props.onClick && this.setProp( 'onClick', this.props.onClick, inputProps );
    this.props.onPointerDown && this.setProp( 'onPointerDown', this.props.onPointerDown, inputProps );

    return (
      <Button
        variant="contained"
        {...inputProps}
      >{innerContent}</Button>
    )
  }
}

FilledButton.defaultProps = {
  disabled: false
}

export default FilledButton;