import React from 'react';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Radio from '@mui/material/Radio';
import TitanConstants from './TitanConstants.js';

class BackgroundSoundRadioGroup extends React.Component {
  constructor( props ) {
    super( props );
  }

  render() {

    // For some reason the label color flashes to the primary main color when you click on the radio buttons,
    // but flickers on and off if you click between the buttons. Use main text colors instead of flashing
    // to the dark main color of the app. This string is used as a lookup for the titanTheme, it will
    // look for palette.text.main.
    const labelColor = 'text';
    return (
      <FormControl>
        <FormLabel id="demo-radio-buttons-group-label" color={labelColor}>Background Sounds</FormLabel>
        <RadioGroup
          defaultValue={TitanConstants.BACKGROUND_NONE}
          name="background-sound-radio-buttons"
          onChange={this.props.handleChange}
        >
          <FormControlLabel value={TitanConstants.BACKGROUND_NONE} control={<Radio/>} label="None"/>
          <FormControlLabel value={TitanConstants.BACKGROUND_WATERFALL} control={<Radio/>} label="Waterfall"/>
          <FormControlLabel value={TitanConstants.BACKGROUND_RAIN} control={<Radio/>} label="Rain"/>
          <FormControlLabel value={TitanConstants.BACKGROUND_FIRE} control={<Radio/>} label="Fire"/>
        </RadioGroup>
      </FormControl>
    );
  }
}

export default BackgroundSoundRadioGroup;