import React from 'react';
import titanTheme from './titanTheme.js';
import Util from './Util.js';

let drawingUtils = window;
let mpHands = window;

let instanceCounter = 0;


class HandCanvas extends React.Component {

  /**
   * props:
   *    - shouldRender {boolean} - whether or not to render/redraw canvas (set to false to optimize)
   *    - landmarkColor {string} - primary CSS color for the landmarks when drawing
   *    - connectorColor {string} - primary CSS color for the connectors when drawing
   * @param props
   */
  constructor( props ) {
    super( props );

    this.id = instanceCounter++;
    this.canvasElementId = `hand-canvas-${instanceCounter}`;

    // You can pass handLandmarks directly to the instance or through props if you
    // must. I am wondering if this kind of animation
    this.handLandmarks = [];

    window.setInterval( () => {
      const landmarks = this.props.landmarks || [];
      this.renderLandmarks( landmarks );
    }, 1 / 60 );
  }

  renderLandmarks( landmarks ) {
    const canvas = document.getElementById( this.canvasElementId );
    if ( canvas ) {
      const context = canvas.getContext( '2d' );
      context.save();

      context.clearRect( 0, 0, canvas.width, canvas.height );

      context.fillStyle = titanTheme.palette.background.default;
      context.fillRect( 0, 0, canvas.width, canvas.height );

      context.strokeStyle = titanTheme.palette.primary.main;
      context.strokeRect( 0, 0, canvas.width, canvas.height );

      // mirror the results for the rest of the drawing so it behaves like a mirror
      context.scale( -1, 1 );
      context.translate( -canvas.width, 0 );

      // wait until dependencies are loaded
      if ( drawingUtils && mpHands ) {
        drawingUtils.drawConnectors(
          context, landmarks, mpHands.HAND_CONNECTIONS, {
            color: ( data ) => {
              const alphaVal = drawingUtils.lerp( data.to.z, -0.15, .1, 1, 0.2 );
              return Util.hexToRGB( this.props.connectorColor, alphaVal );
            },
            thickness: 0.2
          } );
        drawingUtils.drawLandmarks( context, landmarks, {
          color: ( data ) => {
            const alphaVal = drawingUtils.lerp( data.from.z, -0.15, .1, 1, 0.2 );
            return Util.hexToRGB( this.props.landmarkColor, alphaVal );
          },
          radius: ( data ) => {
            return drawingUtils.lerp( data.from.z, -0.15, .1, 2, 0.3 );
          }
        } );
      }

      context.restore();
    }
  }

  render() {
    return (
      <canvas width='250' height='250' id={this.canvasElementId}></canvas>
    );
  }
}

export default HandCanvas;