class LinearFunction {
  constructor( a1, a2, b1, b2, clamp ) {
    this.a1 = a1;
    this.a2 = a2;
    this.b1 = b1;
    this.b2 = b2;
    this.clamp = clamp;

  }

  evaluate( a3 ) {
    return this.map( this.a1, this.a2, this.b1, this.b2, a3, this.clamp );
  }

  linear( a1, a2, b1, b2, a3 ) {
    return ( b2 - b1 ) / ( a2 - a1 ) * ( a3 - a1 ) + b1;
  }

  map( a1, a2, b1, b2, a3, clamp ) {
    let b3 = this.linear( a1, a2, b1, b2, a3 );
    if ( clamp ) {
      const max = Math.max( b1, b2 );
      const min = Math.min( b1, b2 );
      b3 = this.clampValue( b3, min, max );
    }
    return b3;
  }

  clampValue( value, min, max ) {
    if ( value < min ) {
      return min;
    }
    else if ( value > max ) {
      return max;
    }
    else {
      return value;
    }
  }
}

export default LinearFunction;