import Property from './Property.js';

let mpHands = window;

// mediapipe configuration to find the hand detection model (cdn)
const config = {
  locateFile: ( file ) => {
    return `https://cdn.jsdelivr.net/npm/@mediapipe/hands@${mpHands.VERSION}/${file}`;
  }
};

class MediaPipeManager {
  constructor() {

    this.hands = null;
    this.handClassification = null;

    // The current landmarks in world coordinates - the coordinates used for the classification model
    this.worldHandLandmarksProperty = new Property( [] );

    // The current landmarks in normalized coordinates relative to the video feed - used for drawing to
    // another canvas.
    this.drawingLandmarksProperty = new Property( [] );

    this.boundOnResults = this.onResults.bind( this );
  }

  init( videoElement ) {
    const hands = new mpHands.Hands( config );

    hands.setOptions( {
      maxNumHands: 1,

      // workaround for https://github.com/google/mediapipe/issues/2181
      modelComplexity: 1,

      minDetectionConfidence: 0.5,
      minTrackingConfidence: 0.5
    } );

    const camera = new window.Camera( videoElement, {
      onFrame: async () => {
        await hands.send( { image: videoElement } );
      },
      width: 360,
      height: 360
    } );
    camera.start();

    this.hands = hands;

    this.hands.onResults( this.boundOnResults );
  }

  onResults( results ) {
    if ( results.multiHandLandmarks && results.multiHandedness ) {
      if ( results.multiHandLandmarks.length > 0 ) {
        for ( let index = 0; index < results.multiHandLandmarks.length; index++ ) {
          const classification = results.multiHandedness[ index ];

          // Set the landmarks that are used to draw displays - set data regardless of classification
          // so that we always draw something to components watching this field
          if ( !( results.multiHandLandmarks[ index ].length === 0 && this.drawingLandmarksProperty.value.length === 0 ) ) {

            // dont set the value to a new empty array if it is already an empty array
            this.drawingLandmarksProperty.value = results.multiHandLandmarks[ index ];
          }

          // only save and classify data if the mediapipe classification has a decent score
          if ( classification.score > 0.85 ) {

            // The landmarks described by their distance relative to the center of the hand which is good for gesture
            // training because then position in camera frame does not matter.
            this.worldHandLandmarksProperty.value = results.multiHandWorldLandmarks[ index ];
          }
          else {

            // not confident about the classification, clear
            this.clearLandmarks();
          }
        }
      }
      else {

        // no landmarks detected, clear
        this.clearLandmarks();
      }
    }
    else {

      // nothing detected, clear
      this.clearLandmarks();
    }
  }

  clearLandmarks() {
    if ( this.worldHandLandmarksProperty.value.length > 0 ) {
      this.worldHandLandmarksProperty.value = [];
    }
    if ( this.drawingLandmarksProperty.value.length > 0 ) {
      this.drawingLandmarksProperty.value = [];
    }
  }
}

export default MediaPipeManager;