import gestureSound1 from './audio/1.mp3';
import gestureSound10 from './audio/10.mp3';
import gestureSound11 from './audio/11.mp3';
import gestureSound12 from './audio/12.mp3';
import gestureSound13 from './audio/13.mp3';
import gestureSound14 from './audio/14.mp3';
import gestureSound15 from './audio/15.mp3';
import gestureSound16 from './audio/16.mp3';
import gestureSound17 from './audio/17.mp3';
import gestureSound2 from './audio/2.mp3';
import gestureSound3 from './audio/3.mp3';
import gestureSound4 from './audio/4.mp3';
import gestureSound5 from './audio/5.mp3';
import gestureSound6 from './audio/6.mp3';
import gestureSound7 from './audio/7.mp3';
import gestureSound8 from './audio/8.mp3';
import gestureSound9 from './audio/9.mp3';
import fireSound from './audio/campfire-background.mp3';
import rainSound from './audio/rain-background.mp3';
import waterfallSound from './audio/waterfall-background-2.mp3';
import TitanConstants from './TitanConstants.js';

const audioContext = new AudioContext();
const backgroundGainNode = audioContext.createGain();
const gestureGainNode = audioContext.createGain();

// background volume is reduced
backgroundGainNode.gain.value = 0.4;
gestureGainNode.gain.value = 1.0;

// To be converted into audio
const gestureSounds = [
  gestureSound1,
  gestureSound2,
  gestureSound3,
  gestureSound4,
  gestureSound5,
  gestureSound6,
  gestureSound7,
  gestureSound8,
  gestureSound9,
  gestureSound10,
  gestureSound11,
  gestureSound12,
  gestureSound13,
  gestureSound14,
  gestureSound15,
  gestureSound16,
  gestureSound17
];

const backgroundSounds = [
  waterfallSound,
  rainSound,
  fireSound
];

/**
 * Wraps a sound source (BufferSource) with a play function so that loopable sources
 * and Audio can have the same play/pause API for the purposes of this app.
 */
class PlayableAudio {
  constructor( arrayBuffer, gainNode ) {
    this.arrayBuffer = arrayBuffer;
    this.gainNode = gainNode;

    this.audioBuffer = null;
  }

  async prepareAudioBuffer() {
    this.audioBuffer = await audioContext.decodeAudioData( this.arrayBuffer );
  }

  async play() {
    this.source = audioContext.createBufferSource();
    this.source.buffer = this.audioBuffer;
    this.source.loop = true;

    const duration = this.audioBuffer.duration;

    this.source.loopStart = 2;
    this.source.loopEnd = duration - 3;

    // connect to the web audio graph
    this.source.connect( this.gainNode ).connect( audioContext.destination );

    this.source.start();
  }

  pause() {
    if ( this.source ) {

      // stop playing
      this.source.stop();

      // disconnect from web audio graph
      this.source.disconnect();
    }
  }
}

async function processSoundClip( sound, gainNode ) {
  const playableAudio = new Audio( sound );
  const source = audioContext.createMediaElementSource( playableAudio );

  // connect through the gainNode to control volume
  source.connect( gainNode ).connect( audioContext.destination );

  return playableAudio;
}

async function processLoopableSound( soundPath, gainNode ) {

  const arrayBuffer = await fetch( soundPath ).then( ( res ) => res.arrayBuffer() );

  const playableAudio = new PlayableAudio( arrayBuffer, gainNode );
  await playableAudio.prepareAudioBuffer();

  return playableAudio;
}

let GESTURE_AUDIO_CLIPS = null;
let BACKGROUND_AUDIO_CLIPS = null;
let BACKGROUND_AUDIO_MAP = null;
let GESTURE_AUDIO_MAP = null;

class TitanSoundClipPlayer {
  constructor() {
    this.playingAudio = null;
    this.playingBackgroundAudio = null;
    this.playingGesture = null;
  }

  /**
   * Using async (necessary to load web audio for custom playback) is not supported for 'top-level' usage
   * in NOde.js. Use init to
   */
  static async init() {

    GESTURE_AUDIO_CLIPS = await Promise.all( gestureSounds.map( async sound => {
      return await processSoundClip( sound, gestureGainNode );
    } ) );

    BACKGROUND_AUDIO_CLIPS = await Promise.all( backgroundSounds.map( async soundPath => {
      return await processLoopableSound( soundPath, backgroundGainNode );
    } ) );

    // Map a background audio enum to the playable Audio object
    BACKGROUND_AUDIO_MAP = new Map( [
      [ TitanConstants.BACKGROUND_WATERFALL, BACKGROUND_AUDIO_CLIPS[ 0 ] ],
      [ TitanConstants.BACKGROUND_RAIN, BACKGROUND_AUDIO_CLIPS[ 1 ] ],
      [ TitanConstants.BACKGROUND_FIRE, BACKGROUND_AUDIO_CLIPS[ 2 ] ]
    ] )

    // Map each audio clip to a gesture for playing - The audioClips are in the order that they should be played
    // to recreate the 'titan' theme, the gestures are in a random order that feels nice without going "back to back"
    // with like gestures.
    GESTURE_AUDIO_MAP = new Map();
    TitanConstants.TITAN_SONG_GESTURE_ORDER.forEach( ( gesture, index ) => {
      const audioClip = GESTURE_AUDIO_CLIPS[ index ];
      if ( audioClip ) {
        GESTURE_AUDIO_MAP.set( gesture, audioClip );
      }
    } );
  }

  getGestureAudioTimeProperties() {
    if ( this.playingAudio ) {
      return {
        currentTime: this.playingAudio.currentTime,
        duration: this.playingAudio.duration,
        playingGesture: this.playingGesture
      }
    }
    else {
      return null;
    }
  }

  playAudio( audio ) {

    // check if context is in suspended state (autoplay policy)
    if ( audioContext.state === 'suspended' ) {
      audioContext.resume();
    }

    audio.play();
  }

  playGestureSound( gesture ) {
    if ( GESTURE_AUDIO_MAP && GESTURE_AUDIO_MAP.has( gesture ) ) {
      const audio = GESTURE_AUDIO_MAP.get( gesture );
      this.playAudio( audio );
      this.playingAudio = audio;
      this.playingGesture = gesture;

      const endedListener = event => {
        if ( this.playingAudio === audio ) {

          // this audio finished naturally and there is no new audio
          this.playingAudio = null;
          this.playingGesture = null;
        }

        audio.removeEventListener( 'ended', endedListener );
      };
      audio.addEventListener( 'ended', endedListener );
    }
  }

  loopBackgroundSound( backgroundSound ) {
    this.stopBackgroundSound();

    if ( BACKGROUND_AUDIO_MAP && BACKGROUND_AUDIO_MAP.has( backgroundSound ) ) {
      const audio = BACKGROUND_AUDIO_MAP.get( backgroundSound );
      this.playAudio( audio );
      this.playingBackgroundAudio = audio;
    }
  }

  stopBackgroundSound() {
    if ( this.playingBackgroundAudio ) {
      this.playingBackgroundAudio.pause();
      this.playingBackgroundAudio = null;
    }
  }

  stopSound() {
    this.playingAudio.pause();
    this.playingAudio = null;
    this.playingGesture = null;
  }
}

export default TitanSoundClipPlayer;