import AppShortcutIcon from '@mui/icons-material/AppShortcut';
import HubIcon from '@mui/icons-material/Hub';
import BottomNavigation from "@mui/material/BottomNavigation";
import BottomNavigationAction from "@mui/material/BottomNavigationAction";
import Box from "@mui/material/Box";
import { styled } from "@mui/material/styles";
import React from 'react';
import titanTheme from './titanTheme.js';

const StyledBottomNavigationAction = styled( BottomNavigationAction )( `
  color: ${titanTheme.palette.primary.light};
  &.Mui-selected {
    color: ${titanTheme.palette.primary.contrastText};
  }
` );

class ScreenNavigation extends React.Component {

  /**
   * Expected props:
   * @prop {function} handleValueChange
   * @param props
   */
  constructor( props ) {
    super( props );

    this.state = {
      value: this.props.value
    }
  }

  render() {
    return (
      <Box>
        <BottomNavigation
          sx={{ height: '3em' }}
          showLabels
          value={this.state.value}
          onChange={( event, newValue ) => {
            this.setState( {
              value: newValue
            } );
            this.props.handleValueChange( newValue );
          }}
        >
          <StyledBottomNavigationAction label="Training" icon={<HubIcon/>}/>
          <StyledBottomNavigationAction label="Titan" icon={<AppShortcutIcon/>}/>
        </BottomNavigation>
      </Box>
    )
  }
}

export default ScreenNavigation;