import Box from '@mui/material/Box';
import FormControl from '@mui/material/FormControl';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import * as React from 'react';

const SELECT_ITEMS = [];
for ( let i = 0; i < 21; i++ ) {

  // labels are not zero indexed
  SELECT_ITEMS.push( { value: `GESTURE_${i + 1}`, label: `Gesture ${i + 1}`, key: i } );
}

class GestureSelect extends React.Component {

  /**
   * Expected props:
   * @prop {function( event)} onChange
   * @param props
   */
  constructor( props ) {
    super( props );

    this.state = {
      gestureValue: 'GESTURE_1'
    }

    this.props.selectedGestureProperty.lazyLink( selectedGesture => {
      this.setState( { gestureValue: selectedGesture } );
    } );
  }

  render() {
    return (
      <Box sx={{ minWidth: 120 }}>
        <FormControl fullWidth>
          <Select
            labelId="gesture-select"
            value={this.state.gestureValue}
            displayEmpty
            sx={{ width: 1 / 4 }}
            onChange={( event ) => {
              this.props.selectedGestureProperty.value = event.target.value
            }}
          >
            {
              SELECT_ITEMS.map( item => <MenuItem value={item.value} key={item.key}>{item.label}</MenuItem> )
            }
          </Select>
        </FormControl>
      </Box>
    );
  }
}

export default GestureSelect;