import Box from '@mui/material/Box';
import CssBaseline from "@mui/material/CssBaseline";
import Paper from '@mui/material/Paper';
import { ThemeProvider } from '@mui/material/styles';
import React from 'react';
import ReactDOM from 'react-dom/client';
import ClassificationManager from './ClassificationManager.js';
import './index.css';
import LandingDialog from './LandingDialog.js';
import MediaPipeManager from './MediaPipeManager.js';
import ScreenNavigation from './ScreenNavigation.js';
import TitanScreen from './TitanScreen.js';
import titanTheme from './titanTheme.js';
import TrainingScreen from './TrainingScreen.js';

class App extends React.Component {
  constructor( props ) {
    super( props );

    this.classificationManager = new ClassificationManager();
    this.mediaPipeManager = new MediaPipeManager();

    this.boundHandleScreenChange = this.handleScreenChange.bind( this );
    this.boundSetHandLandmarks = this.setHandLandmarks.bind( this );
    this.boundSetClassification = this.setClassification.bind( this );
    this.boundSetHandLandmarksForDrawing = this.setHandLandmarksForDrawing.bind( this );

    this.state = {

      // A reference to the hands object from mediapipe so that subcomponents
      // can add listeners to when we get results.
      hands: null,

      // Current landmark results from mpHands
      handLandmarks: [],

      // One set of landmarks per gesture, so that they can be drawn to show how to play sounds
      handLandmarksForDrawing: {},

      // The classification of a hand gesture once we have a neural network
      classification: { value: null, results: [] },

      // The selected screen
      // 0 - Training screen
      // 1 - Titan screen
      screenValue: 1
    }

    // Add a listener that will start classifying results once the network is trained. Once added this listener
    // is removed
    const networkTrainedListener = trained => {
      if ( trained ) {
        this.mediaPipeManager.worldHandLandmarksProperty.lazyLink( worldLandmarks => {
          this.classificationManager.mlClassify( worldLandmarks );
        } );

        this.classificationManager.nnTrainedProperty.unlink( networkTrainedListener );
      }
    };
    this.classificationManager.nnTrainedProperty.link( networkTrainedListener );

    const addLandmarksListener = newWorldLandmarks => {
      const trainingObject = this.classificationManager.landmarksToTrainingObject( newWorldLandmarks );
      this.classificationManager.addGestureData( [ trainingObject ], [ this.mediaPipeManager.drawingLandmarksProperty.value ], this.classificationManager.selectedGestureProperty.value )
    };

    this.classificationManager.recordingProperty.link( recording => {
      if ( recording ) {
        this.mediaPipeManager.worldHandLandmarksProperty.lazyLink( addLandmarksListener );
      }
      else {
        this.mediaPipeManager.worldHandLandmarksProperty.unlink( addLandmarksListener );
      }
    } );
  }

  setHandLandmarksForDrawing( gesture, landmarks ) {
    this.state.handLandmarksForDrawing[ gesture ] = landmarks;
    this.setState( {
      handLandmarksForDrawing: this.state.handLandmarksForDrawing
    } );
  }

  setClassification( classification ) {
    this.setState( {
      classification: classification
    } );
  }

  setHandLandmarks( landmarks ) {
    this.setState( {
      handLandmarks: landmarks
    } )
  }

  handleScreenChange( screenValue ) {
    this.setState( {
      screenValue: screenValue
    } );
  }

  render() {
    let screenBox;
    if ( this.state.screenValue === 0 ) {
      screenBox = (
        <Box>
          <TrainingScreen
            mediaPipeManager={this.mediaPipeManager}
            classificationManager={this.classificationManager}
          />
        </Box>
      )
    }
    else if ( this.state.screenValue === 1 ) {
      screenBox = (
        <Box>
          <TitanScreen
            mediaPipeManager={this.mediaPipeManager}
            classificationManager={this.classificationManager}
          />
        </Box>
      )
    }

    return (
      <ThemeProvider theme={titanTheme}>
        <CssBaseline/>
        <LandingDialog
          mediaPipeManager={this.mediaPipeManager}
        ></LandingDialog>
        <video hidden id={'input-video'}/>
        {screenBox}
        <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
          <ScreenNavigation
            handleValueChange={this.boundHandleScreenChange}
            value={this.state.screenValue}
          ></ScreenNavigation>
        </Paper>
      </ThemeProvider>
    )
  }

  async componentDidMount() {

    // initialize once the video element is in the DOM
    // TODO: Do we need to wait until mphands is ready?
    // this.mediaPipeManager.init( document.getElementById( 'input-video' ) );

    // try to load model json if we can
    await this.classificationManager.loadModelJSON();

    // train the initial network
    this.classificationManager.createNN();

    // try to load the neural network training data if we can
    await this.classificationManager.loadMLModel();

  }
}

// ========================================

const root = ReactDOM.createRoot( document.getElementById( "root" ) );
root.render( <App/> );
