import saveAs from 'file-saver';
import _ from 'lodash';
import Emitter from './Emitter.js';
// import modelData from './json-model/model-data.json';
import Property from './Property.js';

let ml5 = window.ml5;

class ClassificationManager {
  constructor() {

    // The neural network
    this.nn = null;

    // The selected gesture for training.
    this.selectedGestureProperty = new Property( 'GESTURE_1' );

    // An object containing all training data for the machine learning model, in this form:
    //   trainingModel: {
    //     GESTURE_1: [
    //       { "0x": 0.5, "0y": 0.2, "0z": 0.8, "1x": 0.5, "1y": 0.5, "1z": 0.5, ...,},
    //       { "0x": 0.5, "0y": 0.2, "0z": 0.8, "1x": 0.5, "1y": 0.5, "1z": 0.5, ...,},
    //     ],
    //     GESTURE_2: [
    //       { "0x": 0.5, "0y": 0.2, "0z": 0.8, "1x": 0.5, "1y": 0.5, "1z": 0.5, ...,},
    //       { "0x": 0.5, "0y": 0.2, "0z": 0.8, "1x": 0.5, "1y": 0.5, "1z": 0.5, ...,},
    //     ],
    //      ......
    // }
    this.trainingModel = {};

    // Emits an event whenever the trainingModel is modified
    this.trainingModelChangedEmitter = new Emitter();

    this.classificationProperty = new Property( { label: null, results: null } );

    // A model similar to the one above with with only one saved set of landmarks per gesture.
    // Landmarks are in normalized coordinates and can be used to draw the gesture.
    this.gestureDataForDrawing = {};

    // Whether the neural network has been created and is ready to train.
    this.nnCreatedProperty = new Property( false );

    this.recordingProperty = new Property( false );

    // Whether the neural network has been trained and is ready to classify.
    this.nnTrainedProperty = new Property( false );

    // Whether the neural network has begun to train or load (may not be ready for use yet)
    this.nnTrainingProperty = new Property( false );

    // bound listeners for usage of this in callbacks
    this.boundMLHandleClassifyResults = this.mlHandleClassifyResults.bind( this );

    // this.createNN();
  }

  createNN() {
    if ( window.ml5 ) {

      // clear the old network - I don't see anything about disposing it in documentation
      this.nn = null;

      ml5 = window.ml5;

      const nnOptions = {
        task: 'classification',
        debug: true,
        inputs: 3 * 21, // x, y, z for each gesture
        outputs: 21 // number of gestures
      }
      this.nn = ml5.neuralNetwork( nnOptions );
      this.nnCreatedProperty.value = true;
    }
  }

  /**
   * Adds an array of new data to the trainingModel for the provided gesture.
   * @param newData [] - Data formatted for training the machine learning model
   * @param drawingData [] - An array of all the data to add
   * @param gesture
   */
  addGestureData( newData, drawingData, gesture ) {
    const existingData = this.trainingModel[ gesture ] || [];
    this.trainingModel[ gesture ] = existingData.concat( newData );
    this.trainingModelChangedEmitter.emit();

    // Only one collection is necessary, just set over the old data. Wrapped in an array so it has the
    // same JSON format as the training model
    this.gestureDataForDrawing[ gesture ] = drawingData;
  }

  /**
   * Remove all the data for the provided gesture.
   * @param gesture {string}
   */
  removeAllGestureData( gesture ) {
    this.trainingModel[ gesture ] = [];
    this.gestureDataForDrawing[ gesture ] = [];

    this.trainingModelChangedEmitter.emit();
  }

  /**
   * Get all landmark data for the provided gesture. Returns an array of landmark values.
   */
  getGestureData( gesture ) {
    return this.trainingModel[ gesture ];
  }

  /**
   * For the provided gesture, return a sample of the saved landmarks to draw.
   * @param gesture
   */
  getExampleLandmarksFromGesture( gesture ) {
    let landmarks = [];

    // just return the first one? Maybe average them but doesn't seem worth it...
    const gestureData = this.gestureDataForDrawing[ gesture ];
    if ( gestureData ) {

      // Even though there is only one entry for drawing landmarks it is stored in an array so that the format
      // matches other gesture data for easy processing
      landmarks = gestureData[ 0 ];
    }

    return landmarks;
  }

  /**
   * Convert model to data that can be used to train the ML5 model.
   * @returns {*[]}
   */
  convertModelToML5Data() {
    const ml5Data = [];

    const trainingModel = this.trainingModel;
    for ( const gestureKey in trainingModel ) {
      const gestureDataCollection = trainingModel[ gestureKey ];
      gestureDataCollection.forEach( gestureData => {
        gestureData.gesture = gestureKey;
        ml5Data.push( gestureData );
      } );
    }

    return ml5Data;
  }

  /**
   * Take the saved trainingModel, convert it into a format that can be used by ml5, and
   * train the neural network.
   */
  trainMLModel() {

    // Create a new Neural network
    this.createNN();

    // convert the data to a format that can be provided to ML5js
    const ml5Data = this.convertModelToML5Data();

    // train the model
    ml5Data.forEach( item => {
      const input = {};
      for ( const key in item ) {
        if ( key !== 'gesture' ) {
          input[ key ] = item[ key ];
        }
      }

      const output = {
        gesture: item.gesture
      };

      this.nn.addData( input, output );
    } );

    // normalize before training
    this.nn.normalizeData();

    // train the network from the data
    const trainingOptions = {
      epochs: 60
      // batchSize: 100
    }

    // immediately indicate that no further training is allowed
    this.nnTrainedProperty.value = false;
    this.nnTrainingProperty.value = true;

    this.nn.train( trainingOptions, () => {
      console.log( 'training complete' );
      this.nnTrainedProperty.value = true;
      this.nnTrainingProperty.value = false;
    } );
  }

  /**
   * Save files from the trained model so they can be reused next runtime. Saved files need to be moved
   * from downloads folder into /public/trained-model.
   */
  saveMLModel() {

    // built-in support - move downloads to public/trained-model
    this.nn.save();
  }

  /**
   * Load a machine learning model from saved files.
   */
  async loadMLModel() {
    if ( !this.nnTrainedProperty.value ) {
      const modelData = await fetch( './trained-model/model.json' ).then( response => {
        return response.json();
      } );

      // A quick check to make sure that the model data exists
      if ( modelData.modelTopology ) {
        const modelDetails = {
          model: './trained-model/model.json',
          metadata: './trained-model/model_meta.json',
          weights: './trained-model/model.weights.bin'
        }

        // immediately indicate that we should not allow further training.
        this.nnTrainingProperty.value = true;

        this.nn.load( modelDetails, () => {
          console.log( 'pre-trained model loaded' );
          this.nnTrainedProperty.value = true;
          this.nnTrainingProperty.value = false;
        } )
      }
    }
  }

  /**
   * Try to load the model JSON for this runtime.
   */
  async loadModelJSON() {
    const modelData = await fetch( './json-model/model-data.json' ).then( response => {
      return response.json();
    } );

    if ( modelData.trainingModel ) {
      this.trainingModel = modelData.trainingModel;
      this.trainingModelChangedEmitter.emit();
    }
    if ( modelData.drawingData ) {
      for ( const gesture in modelData.drawingData ) {
        this.gestureDataForDrawing[ gesture ] = modelData.drawingData[ gesture ];
      }
    }
  }

  /**
   * Try to save the model data so it can be easily loaded for the next runtime.
   */
  saveModelJSON() {
    const fileName = 'model-data.json';

    const combinedData = {};

    combinedData.trainingModel = this.trainingModel;
    combinedData.drawingData = this.gestureDataForDrawing;

    // TODO: I am not sure why using modelData directly does't work in React, why the 'x'?
    const blob = new Blob( [ JSON.stringify( combinedData, null, 2 ) ], {
      type: 'application/json',
      name: fileName
    } );

    saveAs(
      blob,
      fileName
    )
  }

  landmarksToTrainingObject( landmarks ) {
    const trainingObject = {};

    landmarks.forEach( ( landmark, i ) => {
      trainingObject[ `${i}x` ] = landmark.x;
      trainingObject[ `${i}y` ] = landmark.y;
      trainingObject[ `${i}z` ] = landmark.z;
    } );

    trainingObject[ 'gesture' ] = this.selectedGestureProperty.value;

    return trainingObject;
  }

  mlClassify( landmarks ) {
    if ( this.nn && this.nnTrainedProperty.value && landmarks.length > 0 ) {
      const inputData = this.landmarksToTrainingObject( landmarks );
      this.nn.classify( inputData, this.boundMLHandleClassifyResults );
    }
  }

  clearClassification() {
    this.classificationProperty.value = { label: null, results: null };
  }

  mlHandleClassifyResults( error, results ) {
    if ( error ) {
      console.error( error );
      return;
    }

    const dominantResult = _.maxBy( results, result => result.confidence );

    let classificationObject = { label: null, confidence: null };
    if ( dominantResult.confidence > 0.85 ) {
      classificationObject = { label: dominantResult.label, confidence: dominantResult.confidence }
    }

    if ( classificationObject.label !== this.classificationProperty.value.label ) {

      // broadcast that the classification has changed
      this.classificationProperty.value = classificationObject;
    }
  }
}

export default ClassificationManager;